.contact-content-wrap{
    display: flex;
    .contact{
        &-img-area{
            width: 58%;
            .brand-row{
                padding-top: 44px;
                padding-right: 100px;
            }
        }
        &-img{
            border: 1px solid $gray-200;
            border-top-left-radius: $border-radius-lg;
            border-bottom-left-radius: $border-radius-lg;
            border-right: 0;
            padding: 16px 100px 16px 16px;
            position: relative;
            z-index: 1;
            img{
                width: 100%;
            }
            &::after{
                position: absolute;
                content: "";
                top: 50%;
                transform: translateY(-50%);
                right: 40px;
                width: 156px;
                height: 156px;
                background: url("#{$directory-path}/shape/contact-img-shape.png") no-repeat;
                z-index: -1;
            }
        }
        &-form-wrap{
            width: 42%;
            background: url("#{$directory-path}/thumb/sky.jpg") no-repeat;
            background-size: cover;
            padding: 40px;
            position: relative;
            &::before {
                position: absolute;
                content: "";
                background-color: $primary;
                bottom: 0;
                right: 0;
                width: 5000px;
                height: 1px;
            }
        }
    }
}

.contact-content-wrap-2 .contact-img-area .brand-row{
    padding-top: 70px;
}

@media (max-width: 767px){
    .contact-content-wrap{
        flex-wrap: wrap;
        gap: 50px;
        .contact-img-area{
            width: 100%;
        }
        .contact-form-wrap{
            width: 100%;
        }
    }
}