.footer-section{
  .container{
    position: relative;
    z-index: 1;
  }
}

.footer{
  &-info{
    background-color: $dark;
    padding: 100px 30px 50px 30px;
    .list-icon{
      li{
        margin-bottom: 12px;
      }
    }
  }
  &-logo{
    margin-bottom: 24px;
  }
  &-item{
    padding-top: 116px;
    .footer-title{
      margin-bottom: 24px;
    }
    &-white{
      h1,h2,h3,h4,h5,h6{
        color: $white;
      }
    }
  }
}

.footer-info-2{
  padding-top: 60px;
  border-top: 8px solid $primary;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin-top: -8px;
}
.footer-item-2{
  padding-top: 74px;
}
// copyright
.copyright{
  &-section{
    padding-top: 12px;
    padding-bottom: 12px;
    &-white{
      p{
        color: $white;
      }
    }
  }
  &-left,
  &-right{
    width: 50%;
  }
  &-right{
    .list-item-inline{
      justify-content: flex-end;
    }
  }
}

@media (max-width: 992px) {
  .footer-item{
    padding-top: 0;
    padding-bottom: 50px;
  }
  .footer-info{
    margin-bottom: 50px;
  }
}

@media (max-width: 767px){
  .copyright-left, 
  .copyright-right{
    width: 100%;
  }
  .copyright-left{
    text-align: center;
    padding-bottom: 6px;
  }
  .copyright-right .list-item-inline{
    justify-content: center;
  }
}
