.breadcrumb{
    &-section{
        padding-top: 200px;
        padding-bottom: 200px;
        position: relative;
        z-index: 1;
        &::after {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            content: "";
            opacity: .6;
            z-index: -1;
            background-color: $dark;
        }
    }
    &-wrap{
        text-align: center;
        .title{
            font-size: 80px;
            color: $white;
            line-height: 1;
        }
    }
    justify-content: center;
    margin-bottom: 0;
    margin-top: 20px;
    &-item{
        font-size: 22px;
        a{
            color: $white;
            &:hover{
                color: $primary;
            }
        }
        &.active {
            color: $white;
        }
        + .breadcrumb-item::before {
            color: $white;
        }
    }
}
@media (max-width: 1440px){
    .breadcrumb{
        &-section{
            padding-top: 100px;
            padding-bottom: 100px;
        }
        &-wrap .title {
            font-size: 40px;
        }
        &-item {
            font-size: 18px;
        }
    }
}
@media (max-width: 575px){
    .breadcrumb{
        margin-top: 10px;
        &-section {
            padding-top: 60px;
            padding-bottom: 60px;
        }
        &-wrap .title {
            font-size: 30px;
        }
        &-item {
            font-size: 16px;
        }
    }
}