.about-img-wrap{
  position: relative;
  z-index: 1;
  padding-right: 18px;
  &::before{
    position: absolute;
    content: "";
    width: 80%;
    height: 108%;
    background-color: $gray-100;
    top: -4%;
    left: -4%;
    z-index: -1;
  }
  &::after{
    position: absolute;
    content: "";
    bottom: -4%;
    left: -4%;
    width: 200px;
    height: 180px;
    background-color: $primary;
    z-index: -1;
  }
}
.about-img-wrap-2{
  margin-right: -256px;
  img{
    height: 764px;
    object-fit: cover;
  }
}

@media (max-width: 1440px){
  .about-img-wrap-2{
    margin-right: 0;
    img{
      height: 733px;
    }
  }
}

@media (max-width: 1024px){
  .about-img-wrap{
    padding-right: 0;
    margin-bottom: 100px;
  }
}