/* Form */
.form {
    &-choice-wrap {
        .choices {
            &__inner {
                border-color: $choice-border-color;
                background-color: $choice-bg;
                padding: 0.20rem 1.25rem;
            }
            &__list {
                &--single {
                    padding: 6px 34px 1px 0;
                    .choices__item {
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                    }
                }
                &--dropdown {
                    border-color: $choice-dropdown-border-color;
                    border-bottom-left-radius: 0.375rem;
                    border-bottom-right-radius: 0.375rem;
                    box-shadow: $choice-dropdown-box-shadow;
                    .choices__item {
                        padding: 6px 20px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        &--selectable {
                            &.is-highlighted {
                                background-color: $choice-item-selected-bg;
                                color: $choice-item-selected-color;
                            }
                        }
                    }
                }
            }
            &[data-type*='select-one'] {
                .choices__inner {
                    padding-bottom: 0;
                }
                .choices__button {
                    background-image: none;
                    right: 36px;
                    opacity: 1;
                    margin-right: 0;
                    text-indent: inherit;
                    color: $white;
                    margin-top: -13px;
                    &::after {
                        position: absolute;
                        content: "\ea06";
                        font-family: $ni-font;
                        top: 0;
                        right: 0;
                        color: $secondary;
                        @include transition(0.3s);
                    }
                    &:hover {
                       &::after {
                           color: $heading-color;
                       }
                    }
                }
                &::after {
                    content: "\e9c5";
                    border: 0;
                    font-family: $ni-font;
                    font-size: 22px;
                    right: 34px;
                    margin-top: -14px;
                    color: $choice-arrow-icon-color;
                }
                &.is-open {
                    &::after {
                        content: "\e9da";
                    }
                    .choices__inner {
                        border-bottom-left-radius: 0;
                        border-bottom-right-radius: 0;
                    }
                    &.is-flipped {
                        .choices__inner {
                            border-bottom-left-radius: 0.375rem;
                            border-bottom-right-radius: 0.375rem;
                        }
                    }
                }
            }
        }
        .is-flipped {
            &.is-open {
                .choices__list--dropdown {
                    border-bottom-left-radius: 0;
                    border-bottom-right-radius: 0;
                }
            }
        }
        &.is-transparent{
            .choices__inner{
                background-color: transparent;
                padding: 0;
                border: 0;
                border-radius: 0;
                min-height: inherit;
            }
            .choices__list--single {
                padding: 0 16px 0 0;
                font-size: 16px;
            }
            .choices[data-type*='select-one']::after {
                margin-top: -17px;
                font-size: 20px;
                right: 15px;
            }
        }
    }
    &-control{
        border: 1px solid $border-gray;
        line-height: 1;
        &:focus{
            box-shadow: none;
        }
        &-wrap{
            position: relative;
            .control-btn{
                position: absolute;
                top: 0;
                right: 0;
                height: 100%;
                .btn{
                    height: 100%;
                    padding-left: 16px;
                    padding-right: 16px;
                }
            }
        }
    }
}

// form
.form-group {
    margin-bottom: 20px;
    &:last-child{
        margin-bottom: 0;
    }
} 
.form-control-s1{
    border-radius: 0;
    &:focus{
        box-shadow: none;
    }
    .choices__inner{
        border-radius: 0;
    }
    .choices__list--single {
        padding: 10px 4px;
    }
}
textarea.form-control {
    min-height: 130px;
}
