.user-admin{
  &-card{
    background-color: $user-admin-card-bg;
    border-top-left-radius: $user-admin-card-top-left-border-radius;
    border-bottom-left-radius: $user-admin-card-bottom-left-border-radius;
    padding: 10px;
    position: relative;
    .title{
      font-size: 18px;
      font-weight: 600;
      color: #000;
    }
    &-inner{
      display: flex;
      align-items: center;
      gap: 10px;
    }
  }
  &-meta{
    position: absolute;
    top: 25px;
    right: -25px;
    height: 34px;
    background-color: $user-admin-meta-bg;
    color: $user-admin-meta-color;
    width: max-content;
    transform: rotate(-90deg);
    padding: 4px 12px;
  }
}

.team-single-entry-wrap{
  img{
    width: 100%;
  }
  h4{
    margin-bottom: 20px;
  }
  .row{
    margin-bottom: 30px;
  }
  p{
    margin-bottom: 30px;
  }
}
.team-single-contact{
  &-box{
    border-radius: $border-radius;
    border: 1px solid $gray-100;
    padding: 30px;
  }
  &-list {
    > li{
      display: flex;
      gap: 14px;
      margin-bottom: 10px;
      &:last-child{
        margin-bottom: 0;
      }
      .title{
        font-weight: 500;
        color: $heading-color;
      }
    }
  }
}
