.set-bg{
    background-size: cover;
    background-position: center;
}
.skill-wrap.has-bg{
    position: relative;
    z-index: 1;
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("#{$directory-path}/bg/working-bg.png") no-repeat;
        background-size: cover;
        z-index: -1;
    }
}

// team section bg
.team-section.has-bg{
    position: relative;
    z-index: 1;
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("#{$directory-path}/bg/team-bg.png") no-repeat;
        z-index: -1;
    }
}
// footer section bg
.footer-section.has-bg{
    position: relative;
    z-index: 1;
    &::before,
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    &::before{
        background-color: $dark;
        opacity: 0.9;
    }
    &::after{
        background: url("#{$directory-path}/bg/footer-bg.jpg") no-repeat;
        z-index: -1;
        background-size: cover;
    }
}
.footer-shape{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6px;
    background: url("#{$directory-path}/shape/footer-shape.png") no-repeat;
    background-size: cover;
}

// service bg
.service-section.has-bg{
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        content: "";
        background: url(#{$directory-path}/bg/service-bg.jpg) no-repeat;
        height: 52.5%;
        z-index: -1;
    }
}  

// video bg
.watch-video-section.has-bg{
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        content: "";
        background: url(#{$directory-path}/bg/video-bg.jpg) no-repeat;
        height: 100%;
        z-index: -1;
        background-size: cover;
    }
    &::after {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $dark;
        content: "";
        opacity: .7;
        z-index: -1;
    }
} 

.video-btn-area.has-shape{
  position: absolute;
  display: inline-block;
  z-index: 1;
  top: -84px;
  left: 50%;
  transform: translateX(-50%);
  &:after{
    position: absolute;
    content: "";
    bottom: -50px;
    margin-left: -150px;
    height: 350px;
    width: 90px;
    background-color: $primary;
    border-radius: 380px;
    transform: rotate(-40deg);
    z-index: -1;
  }
}

// working section 2
.working-section-2.has-bg{
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        content: "";
        background: url(#{$directory-path}/bg/working-bg-2.jpg) no-repeat;
        height: 67%;
        z-index: -1;
        background-size: cover;
        background-position: center;
    }
}

// pricing bg
.pricing-section.has-bg{
    position: relative;
    z-index: 1;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        content: "";
        background: url(#{$directory-path}/bg/pricing-bg.jpg) no-repeat;
        height: 100%;
        z-index: -1;
        background-size: cover;
        background-position: center;
    }
}
