.list{
  &-bullet{
    li{
      position: relative;
      padding-left: 20px;
      &:not(:last-child){
        margin-bottom: 4px;
      }
      &::after{
        position: absolute;
        content: "";
        top: 9px;
        left: 0;
        width: 8px;
        height: 8px;
        background-color: $primary;
        border-radius: $border-radius-full;
      }
    }
  }
  &-icon{
    li{
      display: flex;
      gap: 10px;
      align-items: center;
      &:not(:last-child){
        margin-bottom: 6px;
      }
      .icon,
      .icon-element{
        flex-shrink: 0;
      }
    }
    &-white{
      li{
        color: $white;
      }
    }
  }
  &-item{
    >*{
      margin-bottom: 10px;
      a{
        color: inherit;
        a{
          &:hover{
            color: $primary;
          }
        }
      }
    }
    &-white{
      >*{
        color: $white;
        a{
          &:hover{
            color: $primary;
          }
        }
      }
    }
    &-inline{
      display: flex;
      align-items: center;
      >*{
        margin-bottom: 0;
        margin-right: 20px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
  &-button{
    li{
      margin-bottom: 16px;
      &:last-child{
        margin-bottom: 0;
      }
      a{
        display: flex;
        align-items: center;
        gap: 10px;
        background-color: $white;
        padding: 10px 10px;
        border-radius: $border-radius-pill;
        font-size: 18px;
        font-weight: 500;
        transition: 0.2s;
        color: $heading-color;
        .icon{
          font-size: 28px;
        }
        &:hover{
          background-color: $primary;
          color: $white;
        }
      }
    }
  }
  &-item-category{
    a{
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span{
        display: inline-flex;
        align-items: center;
        gap: 6px;
      }
      &:hover{
        color: $primary;
      }
    }
  }
  &-tag{
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    li{
      a{
        border: 1px solid $border-gray;
        background-color: $white;
        border-radius: $border-radius-sm;
        display: block;
        padding: 6px 12px;
        line-height: 1;
        color: $body-color;
        font-size: 15px;
        &:hover{
          background-color: $primary;
          color: $white;
          border-color: $primary;
        }
      }
    }
  }
  &-meta{
    display: flex;
    align-items: center;
    gap: 20px;
    .badge{
      border-radius: $border-radius-pill;
      text-transform: uppercase;
      padding: 8px 16px;
      font-weight: 500;
      font-size: 14px;
      color: $white;
    }
    a{
      color: $body-color;
    }
  }
}
@media (max-width: 767px){
  .list-meta{
    flex-wrap: wrap;
    gap: 10px;
  }
}