//single-service-list-wrap
.single-service-list-wrap{
    background-color: $gray-100;
    height: 100%;
    padding: 30px;
    .heading{
      margin-bottom: 24px;
      margin-top: 10px;
    }
}

.single-entry-wrap{
    img{
        width: 100%;
    }
    .row{
        margin-bottom: 30px;
        + .row{
            margin-top: 40px;
        }
    }
    p{
        margin-bottom: 20px;
        + .row{
            margin-top: 30px;
        }
    }
    .single-service-img-shape{
        position: relative;
        z-index: 1;
        padding-left: 10px;
        &::before {
            position: absolute;
            top: -10px;
            left: 0;
            height: 105%;
            width: 100px;
            content: "";
            background-color: $primary;
            z-index: -1;
        }
    }
    .list-icon{
        li{
            margin-bottom: 20px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        p{
            margin-bottom: 0;
        }
    }
    .service-card-icon img {
        width: auto;
    }
}

.project-single-entry-wrap{
    h4{
        margin-top: 30px;
        margin-bottom: 20px;
    }
    img{
        width: 100%;
    }
    p{
        margin-bottom: 20px;
    }
    .accordion-media .media-body p{
        margin-bottom: 0;
    }
    .row{
        margin-top: 30px;
        margin-bottom: 30px;
    }
}

.blog-single-entry{
    img{
        width: 100%;
    }
    .list-meta{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .blog-head-title{
        margin-bottom: 16px;
        h4{
            line-height: 1.4;
        }
    }
    .service-card-icon{
        img{
            width: auto;
        }
    }
    p{
        margin-bottom: 16px;
    }
    .row{
        margin-top: 30px;
        margin-bottom: 30px;
    }
}

