.overlay{
  background-color: #060a20;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

// loader
.loader-container {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: $white;
  top: 0;
  left: 0;
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  width: 34px;
  height: 34px;
  border: 3px solid $primary;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 

.is-dark{
  .section-sub-title,
  .section-title,
  .section-text,
  .media .title,
  .media .text {
    color: $white;
  }
}
.page-wrap{
  overflow: hidden;
}
// faq
.faq-img-wrap{
  img{
    height: 544px;
    object-fit: cover;
  }
}

// skill progress
.skill-wrap{
  padding: 40px 20px 40px 20px;
}
.skill-progress-item{
  text-align: center;
  .title{
    margin-top: 12px;
  }
  input{
    color: $heading-color !important;
    font-family: $heading-font !important;
  }
}

// filter
.filter{
  &-button-group{
    display: flex;
    align-items: center;
    gap: 10px;
    button{
      background-color: transparent;
      border: 0;
      font-weight: 500;
      padding: 6px 14px;
      line-height: 1;
      @include transition(0.2s);
      &.is-checked{
        background-color: $primary;
        color: $white;
      }
    }
  }
  &-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: -14px;
    margin-right: -14px;
    .filter-item{
      width: 20%;
      padding-left: 14px;
      padding-right: 14px;
      margin-bottom: 24px;
    }
    &.column-3{
      .filter-item{
        width: 33%;
      }
    }
  }
}

// back to top
.back-to-top {
  position: fixed;
  right: 40px;
  bottom: 40px;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px rgba(160, 160, 160, 0.3);
  z-index: 1024;
  transform: translateY(20px);
  transition: all 200ms linear;
  opacity: 0;
  visibility: hidden;
  &::after{
    position: absolute;
    content: $ni-arrow-long-up;
    font-family: $ni-font;
    text-align: center;
    font-size: 20px;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    z-index: 1;
    transition: all 200ms linear;
    align-items: center;
    justify-content: center;
  }
  svg {
    path {
      fill: none;
      stroke: $primary;
      stroke-width: 4;
      transition: all 200ms linear;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    &::after{
      color: $primary;
    }
  }
}

// rating  
.rating{
  display: inline-flex;
  align-items: center;
  gap: 6px;
  &.sm{
    gap: 3px;
    li{
      font-size: 14px;
    }
  }
  &-wrap{
    display: flex;
    align-items: center;
    gap: 8px;
    &.sm{
      gap: 6px;
      .rating-value{
        font-size: 14px;
      }
    }
  }
  li{
    font-size: 16px;
    color: $rating-color;
    line-height: 1;
    &.checked{
      color: $rating-checked-color;
    }
  }
  &-value{
    color: $primary;
  }
}

// pulse anim
.pulse-anim {
  position: relative;
  z-index: 1;
  display: inline-block;
  &::after,
  &::before{
    position: absolute;
    content: '';
    top: -20px;
    left: -20px;
    bottom: -20px;
    right: -20px;
    background-color: rgba(255,255,255,.2);
    border-radius: 50%;
    animation: play-button-pulse 1.8s linear infinite;
    opacity: 0;
    z-index: -1;
  }
  &::after{
    animation-delay: .6s;
  }
  &.gray{
    &::after,
    &::before{
      background-color: #f2f2f2;
    }
  }
}

@keyframes play-button-pulse {
  0% {
    transform: scale(.5);
    opacity: 0
  }

  50% {
    opacity: 1
  }

  100% {
    transform: scale(1.2);
    opacity: 0
  }
}

// divider
.divider {
  height: 1px;
  width: 100%;
  background-color: $gray-200;
  &.has-line{
    position: relative;
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      width: 50px;
      height: 100%;
      content: "";
      background-color: $primary;
    }
    &.is-right{
      &::before{
        left: auto;
        right: 0;
      }
    }
  }
}


// service-note
.service-note {
  position: relative;
  border-left: 2px solid $primary;
  padding: 7px 0 7px 15px;
  z-index: 1;
  span{
    font-weight: 500;
    color: $primary;
  }
  &::after{
    position: absolute;
    z-index: -1;
    content: "";
    top: 0;
    left: 0;
    width: 100px;
    height: 100%;
    background: linear-gradient(90deg, rgba(244,244,244,1) 0%, rgba(255,255,255,1) 100%, rgba(244,244,244,1) 100%);
  }
}

.home-page-2{
  .container{
    max-width: 1280px;
  }
}

//video section
.watch-video-section{
  padding-top: 184px;
  padding-bottom: 100px;
  position: relative;
  overflow: hidden;
}
.watch-video-content{
  .section-text{
    max-width: 550px;
    margin-left: auto;
    margin-right: auto;
  }
  &-btn{
    padding-top: 30px;
  }
}

.transparent-text{
  font-size: 126px;
  font-weight: 600;
  text-transform: uppercase;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $white;
  -webkit-text-fill-color: transparent;
  font-family: $heading-font;
  opacity: 0.2;
  position: absolute;
  top: -90px;
  left: 50px;
  &.right-align{
    left: auto;
    right: 100px;
  }
}

// cart icon
.cart-icon-wrap {
  position: relative;
  display: inline-block;
  .icon{
    font-size: 22px;
  }
  .count-badge{
    position: absolute;
    top: -5px;
    right: -6px;
    background-color: $dark;
  }
}

// map
#init-map{
  width: 100%;
  height: 380px;
}
.leaflet-touch {
  .leaflet-bar {
    border: 0 !important;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.4) !important;
    a:first-child {
      border-top-left-radius: 6px !important;
      border-top-right-radius: 6px !important;
    }
    a:last-child {
      border-bottom-left-radius: 6px !important;
      border-bottom-right-radius: 6px !important;
    }
  }
}

// error
.error-content{
  img{
    width: 100%;
    margin-bottom: 30px;
  }
  .title{
    margin-bottom: 12px;
  }
  .text{
    margin-bottom: 20px;
  }
}

// comments
.comments{
  li:not(:last-child) {
    border-bottom: 1px solid $gray-100;
    padding-bottom: 24px;
    margin-bottom: 24px;
  }
  ul {
    margin-top: 24px;
    padding-top: 24px;
    border-top: 1px solid $gray-100;
    margin-left: 94px;
  }
}
.comment {
  display: flex;
  align-items: flex-start;
  text-align: left;
  gap: 14px;
  .avatar {
    width: 80px;
    height: 80px;
    flex-shrink: 0;
  }
  &-title{
    margin-bottom: 6px;
  }
  &-desc{
    margin-bottom: 10px;
  }
  &-meta {
    font-size: 14px;
    opacity: 0.8;
    &-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &-replay-btn{
    font-size: 14px;
  }
}

// pricing
.pricing-row{
  display: flex;
  flex-wrap: wrap;
}
.pricing-col{
  width: 33%;
}

.working-content-wrap{
  display: flex;
}

@media (max-width: 1440px){
  .faq-img-wrap img{
    height: 588px;
  }
}

@media (max-width: 1024px){
  .filter-container{
    .filter-item{
      width: 33.33333%;
    }
  }
  .working-content-wrap{
    flex-wrap: wrap;
  }
  .transparent-text{
    display: none;
  }
}
@media (max-width: 991px){
  .pricing-col {
    width: 50%;
  }
}

@media (max-width: 767px){
  .filter-button-group{
    flex-wrap: wrap;
  }
  .filter-container{
    .filter-item{
      width: 50%;
    }
  }
  .filter-container.column-3 .filter-item{
    width: 50%;
  }
  .pricing-row{
    gap: 32px;
  }
  .pricing-col {
    width: 100%;
  }
}

@media (max-width: 575px){
  .filter-container{
    .filter-item{
      width: 100%;
    }
  }
  .filter-container.column-3 .filter-item{
    width: 100%;
  }
  .comments ul {
    margin-left: 0;
  }
}

@media (max-width: 425px){
  .comment-meta-wrap{
    flex-wrap: wrap;
    flex-direction: column;
    > *{
      width: 100%;
    }
  }
}
