.brand{
  &-body{
    padding-top: 60px;
    padding-bottom: 70px;
    border-bottom: 1px solid $gray-200;
  }
  &-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
    gap: 30px;
  }
}
@media (max-width: 425px){
  .brand-row{
    justify-content: center;
  }
}