// service card
.service-card{
  background-color: #f3f3f3;
  position: relative;
  z-index: 1;
  &::after{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    content: "";
    background: url("#{$directory-path}/bg/service-card-bg.jpg") no-repeat;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    @include transition(0.3s);
  }
  &-head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: relative;
    border-bottom: 1px solid $border-gray;
    @include transition(0.3s);
  }
  &-body{
    padding: 20px;
    position: relative;
  }
  &-icon{
    background: mix($primary, $white, 15%);
    position: absolute;
    top: 0;
    right: 0;
    height: 101%;
    width: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition(0.3s);
  }
  &-count{
    font-size: 40px;
    font-weight: 600;
    position: absolute;
    bottom: 20px;
    right: 54px;
    color: $white;
    font-family: $heading-font;
    opacity: 0;
    visibility: hidden;
    @include transition(0.3s);
  }
  .title{
    a{
      color: inherit;
      &:hover{
        color: $primary;
      }
    }
  }
  .text{
    margin-bottom: 14px;
  }
  .btn{
    position: absolute;
    bottom: 40px;
    right: -40px;
    transform: rotate(-90deg);
    @include transition(0.3s);
  }
  &:hover{
    .service-card{
      &-head{
        background-color: $dark;
      }
      &-icon{
        background-color: $primary;
        img{
          filter: brightness(1000%);
        }
      }
      &-count{
        opacity: 1;
        visibility: visible;
        bottom: 24px;
      }
    }
    .title{
      color: $white;
    }
    .btn-dark{
      background-color: $primary;
      border-color: $primary;
    }
    &::after{
      opacity: 1;
      visibility: visible;
    }
  }
}

//service card 2 
.service-card-2{
  background-color: $white;
  .service-card-footer{
    img{
      width: 100%;
    }
  }
  .service-card-body{
    transition: all 0.3s;
  }
  &::after{
    display: none;
  }
  &:hover{
    .service-card-head{
      border-bottom-color: $primary;
    }
    .service-card-body{
      background-color: $primary;
      color: $white;
    }
    .list-icon .icon{
      color: $white !important;
    }
    .btn-dark {
      background-color: $dark;
      border-color: $dark;
    }
  }
}

// team card
.team-card{
  text-align: center;
  .icon-list{
    justify-content: center;
  }
  .team-card-img-wrap{
    position: relative;
    z-index: 1;
    &::before{
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      width: 160px;
      height: 20px;
      background-color: $gray-900;
      transform: translateX(-50%);
    }
    &::after{
      position: absolute;
      content: "";
      bottom: -54px;
      left: 50%;
      transform: translateX(-50%);
      width: 160px;
      height: 160px;
      border-radius: 100%;
      background-color: mix($primary, $white, 10%);
      z-index: -1;
      transition: 0.2s;
    }
    img{
      width: auto !important;
      height: 200px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .team-card-body{
    position: relative;
    z-index: 1;
    padding: 20px 20px 30px 20px;
    .title{
      margin-bottom: 4px;
    }
    .meta{
      display: block;
      margin-bottom: 20px;
    }
    &::after{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $gray-100;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: -1;
      clip-path: polygon(20% 0, 80% 0, 100% 100%, 0% 100%);
    }
  }
  &:hover{
    .team-card-img-wrap{
      &:after{
        background-color: $primary;
      }
    }
  }
}

// team card 2
.team-card-2{
  background-color: $gray-100;
  .title{
    margin-bottom: 4px;
  }
  .team-card-img-wrap{
    position: relative;
    border: 8px solid #fbd8bd;
    transition: 0.3s;
    &::after{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $dark;
      opacity: 0;
      transition: 0.3s;
    }
  }
  .icon-list{
    position: absolute;
    top: 20px;
    right: 20px;
    flex-direction: column;
    z-index: 1;
    opacity: 0;
    transition: 0.3s;
  }
  .team-card-body{
    padding: 20px;
  }
  .list-icon{
    border-top: 1px solid $border-gray;
    padding-top: 14px;
    margin-top: 14px;
    .icon{
      color: $primary;
    }
  }
  &:hover{
    .team-card-img-wrap{
      border-color: $primary;
      &::after{
        opacity: 0.5;
      }
      .icon-list{
        opacity: 1;
      }
    }
  }
}

// portfolio card
.portfolio-card{
  position: relative;
  &::before,
  &:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: visible;
    @include transition(0.4s);
  }
  &::before{
    transform: scale(0.5);
    height: 5px;
    background: $primary;
    z-index: 1;
  }
  &::after{
    height: 100%;
    background: rgba($dark, 0.5);
  }
  &-img-wrap{
    img{
      width: 100%;
    }
  }
  &-body{
    position: absolute;
    bottom: 10px;
    left: 20px;
    right: 20px;
    background: $primary;
    color: $white;
    border-radius: $border-radius;
    padding: 20px;
    @include transition(0.4s);
    opacity: 0;
    visibility: hidden;
    z-index: 1;
    .title{
      margin-bottom: 4px;
      color: $white;
    }
    .portfolio-card-icon{
      position: absolute;
      bottom: -14px;
      right: 20px;
      border: 1px solid transparent;
      &:hover{
        border-color: $white;
      }
    }
  }
  &:hover{
    &::before,
    &::after,
    .portfolio-card-body{
      opacity: 1;
      visibility: visible;
    }
    &::before{
      transform: scale(1);
    }
    .portfolio-card-body{
      bottom: 20px;
    }
  }
}

// portfolio card 2
.portfolio-card-2{
  border-radius: $border-radius-lg;
  img{
    border-radius: $border-radius-lg;
  }
  .portfolio-card-body{
    border-radius: $border-radius-pill;
    padding-left: 40px;
    padding-right: 40px;
    .portfolio-card-icon {
      bottom: 50%;
      transform: translateY(50%);
    }
  }
  &::before{
    display: none;
  }
  &::after{
    border-radius: $border-radius-lg;
  }
}

// testimonial card
.testimonial-card{
  position: relative;
  z-index: 1;
  padding: 16px;
  text-align: center;
  &::after{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 180px;
    content: "";
    background-color: $testimonial-card-after-bg;
    z-index: -1;
    @include transition(0.3s);
  }
  .text{
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: italic;
  }
  .title{
    margin-bottom: 6px;
  }
  &-content{
    position: relative;
    padding: 80px 30px 0 30px;
    z-index: 1;
    background-color: $testimonial-card-content-bg;
    &::after{
      position: absolute;
      content: "";
      bottom: -102px;
      left: 0;
      width: 100%;
      background: url("#{$directory-path}/bg/testimonial-card-bg.png") no-repeat;
      z-index: -1;
      height: 102px;
      background-size: contain;
    }
  }
  &-quote{
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    background-color: $testimonial-card-quote-bg;
    border-radius: 100%;
    font-size: 40px;
    display: flex;
    align-items: self-end;
    justify-content: center;
    padding: 10px;
    color: $testimonial-card-quote-color;
    width: 100px;
    height: 100px;
    @include transition(0.3s);
    &::after{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 50%;
      content: "";
      background-color: $testimonial-card-quote-after-bg;
      @include transition(0.3s);
    }
  }
  &-author{
    width: 100px;
    height: 100px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 120px;
    border-radius: 100%;
    img{
      width: 100%;
      border-radius: inherit;
    }
  }
  &:hover{
    .testimonial-card-quote{
      background-color: $testimonial-card-quote-hover-bg;
      color: $white;
      &::after{
        background-color: $testimonial-card-quote-after-hover-bg;
      }
    }
    &::after{
      background-color: $testimonial-card-after-hover-bg;
    }
  }
}


// working card
.working-card{
  border: 1px solid $border-gray;
  border-left: 0;
  position: relative;
  padding: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: $white;
  &-video{
    width: 700px;
    position: relative;
    &-icon{
      position: absolute;
      top: 30px;
      right: 30px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-value {
    font-size: 75px;
    font-weight: 600;
    position: absolute;
    top: 140px;
    right: 40px;
    color: transparent;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -webkit-text-stroke: 1px $gray-200;
    @include transition(0.1s);
  }
  &-icon{
    margin-bottom: 30px;
  }
  .title{
    margin-bottom: 12px;
  }
  .text{
    margin-bottom: 26px;
  }
  &:hover{
    .working-card-value{
      -webkit-text-stroke-color: $gray-900;
    }
    .btn-gray{
      background-color: $primary;
      color: $white !important;
    }
  }
}

.working-card-2{
  border-top: 0;
  border-right: 0;
  border-bottom-color: $primary;
  .working-card-bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("#{$directory-path}/bg/working-card-bg.png") no-repeat;
    background-size: cover;
    opacity: 0;
    transition: 0.3s;
  }
  .working-card-value{
    top: 90px;
  }
  .working-card-btn-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &::before,
  &::after{
    position: absolute;
    content: "";
    bottom: 0;
    width: 1px;
    height: 106px;
    background-color: $primary;
    left: 0;
    transition: 0.3s;
  }
  &::after{
    left: auto;
    right: 0;
  }
  &:hover{
    &::after,
    &::before{
      height: 100%;
    }
    .working-card-bg{
      opacity: 1;
    }
  }
}

// blog card
.blog-card{
  position: relative;
  @include transition(0.2s);
  border: 1px solid $blog-card-border-color;
  .title{
    margin-bottom: 10px;
    a{
      color: inherit;
      &:hover{
        color: $blog-card-title-color;
      }
    }
  }
  &-head{
    padding: 10px 10px 0 10px;
    img {
      width: 100%;
    }
    &-inner{
      position: relative;
      &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $dark;
        opacity: 0;
        @include transition(0.2s);
      }
    }
  }
  &-cat{
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 12px;
    text-transform: uppercase;
    background-color: $blog-card-cat-bg;
    border-radius: $blog-card-cat-border-radius;
    line-height: 1;
    padding: 6px 14px;
    font-weight: 500;
    @include transition(0.2s);
    z-index: 1;
  }
  &-body{
    padding: 20px;
  }
  &-meta{
    background-color: $blog-card-meta-bg;
    line-height: 1;
    padding: 4px 8px 4px 20px;
    font-size: 15px;
    position: relative;
    margin-left: -21px;
    @include transition(0.2s);
    &::after{
      position: absolute;
      content: "";
      right: -10px;
      top: 0;
      width: 0;
      height: 0;
      border-top: 11px solid transparent;
      border-bottom: 12px solid transparent;
      border-left: 11px solid $blog-card-meta-after-color;
      @include transition(0.2s);
    }
  }
  &-comment{
    font-size: 15px;
  }
  &-meta-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &-footer{
    border-top: 1px solid $blog-card-footer-border-color;
    padding: 12px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &:hover{
    background-color: $blog-card-hover-bg;
    .btn-gray{
      background-color: $primary;
      color: $white !important;
    }
    .blog-card{
      &-cat{
        background-color: $blog-card-cat-hover-bg;
        color: $blog-card-cat-hover-color;
      }
      &-meta{
        background-color: $blog-card-meta-hover-bg;
        color: $blog-card-meta-hover-color;
        &::after{
          border-left-color: $blog-card-meta-after-hover-color;
        }
      }
      &-head-inner{
        &:after{
          opacity: 0.5;
        }
      }
    }
  }
}


// shop card
.shop-card{
  .ribbon{
    position: absolute;
    top: 0;
    right: 0;
    background-color: $dark;
    padding: 6px 8px;
    color: $white;
    width: 52px;
    line-height: 1;
    &.new{
      top: 30px;
      background-color: $primary;
    }
  }
  .shop-card-img-area{
    border: 1px solid $border-gray;
    padding: 50px;
    position: relative;
    img{
      width: 260px;
      height: 260px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .cart-icon-wrap{
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .shop-card-content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .title{
    margin-bottom: 4px;
  }
  .add-to-cart-btn{
    border-radius: 0;
    padding-left: 30px;
    padding-right: 30px;
  }
  &:hover{
    .add-to-cart-btn{
      background-color: $primary;
      color: $white !important;
    }
  }
}


// blog card 2
.blog-card-2{
  border: 0;
  .blog-card-head{
    padding: 0;
  }
} 

// pricing card
.pricing-card{
  background-color: $white;
  padding: 0 60px 60px 60px;
  position: relative;
  border-radius: $border-radius;
  &.active-pricing{
    background-color: $dark;
    color: $white;
    z-index: 1;
    margin-top: -16px;
    transform: scale(1.05);
    &::before{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url("#{$directory-path}/bg/pricing-card-bg.png") no-repeat;
      background-size: cover;
      z-index: -1;
      border-radius: $border-radius;
    }
    .pricing-card-price,
    .title{
      color: $white;
    }
  }
  &-icon{
    width: 100px;
    height: 110px;
    background-color: $primary;
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
    text-align: center;
    padding: 22px;
    margin-bottom: 30px;
    position: relative;
    &::before{
      position: absolute;
      top: -10px;
      left: -10px;
      width: 120%;
      height: 10px;
      background-color: $primary;
      content: "";
      clip-path: polygon(7% 0, 94% 0, 100% 100%, 0% 100%);
    }
  }
  &-price{
    position: absolute;
    top: 50px;
    right: 60px;
    font-size: 32px;
    font-weight: 700;
    color: $heading-color;
    font-family: $heading-font;
    span{
      font-size: 16px;
      font-weight: 500;
    }
  }
  .title{
    margin-bottom: 20px;
  }
  &-list{
    li{
      margin-bottom: 8px;
      display: flex;
      align-items: center;
      gap: 10px;
      .ni-cross-circle{
        color: $red-500;
      }
      .ni-check-circle{
        color: $green-500;
      }
    }
  }
  &-btn{
    margin-top: 34px;
    .btn{
      width: 100%;
      text-align: center;
      justify-content: center;
    }
  }
}

@media (min-width: 1400px) {
  .shop-card .shop-card-content{
    padding-left: 0;
    padding-right: 0;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
}

@media (max-width: 1440px) {
  .working-card-video {
    width: 350px;
  }
}

@media (max-width: 1024px) {
  .service-card-icon{
    display: none;
  }
  .working-card-video {
    width: 100%;
  }
  .blog-card-meta-wrap{
    flex-wrap: wrap;
  }
  .shop-card .shop-card-img-area {
    img{
      height: 100px;
    }
  }
  .shop-card .shop-card-content{
    flex-wrap: wrap;
    gap: 10px;
    padding-left: 0;
  }
}

@media (max-width: 991px){
  .shop-card .shop-card-img-area img {
    height: 140px;
  }
  .team-card .team-card-body::after{
    width: 80%;
  }
  .pricing-card.active-pricing{
    transform: scale(1);
  }
}
@media (max-width: 767px){
  .testimonial-card{
    &-content{
      padding-bottom: 30px;
      &::after{
        display: none;
      }
    }
    &-author{
      margin-top: 30px;
    }
  }
  .team-card .team-card-body::after{
    width: 100%;
  }
  .pricing-card.active-pricing{
    margin-top: 0;
  }
}

@media (max-width: 575px){
  .testimonial-card{
    &-content{
      padding-bottom: 65px;
    }
    &-author{
      margin-top: -40px;
      z-index: 1;
      position: relative;
      border: 8px solid $white;
    }
  }
  .shop-card .shop-card-img-area img {
    height: 200px;
  }
  .team-card .team-card-body::after {
    width: 49%;
  }
}
@media (max-width: 425px){
  .service-card {
    .list-icon{
      padding-bottom: 20px;
    }
    .btn{
      position: inherit;
      transform: rotate(0);
      right: auto;
      bottom: auto;
    }
  }
  .team-card .team-card-body::after {
    width: 67%;
  }
}

@media (max-width: 375px){
  .team-card .team-card-body::after {
    width: 77%;
  }
}

@media (max-width: 320px){
  .team-card .team-card-body::after {
    width: 100%;
  }
  .team-card .team-card-img-wrap::before{
    width: 176px;
  }
}





